img {
  width: 100%;
}

.search-on-homepage {
  cursor: pointer;
}

.section-catalog {
  background-color: var(--background-catalog);

  &_hidden {
    display: none;
    visibility: hidden;
  }
}

.search-block {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding-bottom: 16px;

  @include mobile {
    display: flex;
    justify-content: center;
    align-items: center;

    padding-bottom: 16px;
    gap: 16px;
  }

  @include tablet {
    padding-bottom: 36px;
    gap: 16px;
  }

  @include desktop {
    padding-bottom: 48px;
  }

  &_search-form {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    display: contents;

    @include mobile {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      gap: 16px;
      display: contents;
    }
  }

  &_search-input {
    width: 100%;
    height: 44px;
    padding-left: 20;
    font-size: 14px;
    border-radius: 8px;
    line-height: 1.14;

    @include tablet {
      width: 210px;
    }

    @include desktop {
      width: 320px;
      height: 48px;
      padding-left: 24px;
      font-size: 18px;
    }
  }

  &_submit-btn {
    width: 40px;
    height: 40px;
    background-color: $mainLightColor;
    border-radius: 100%;
    cursor: pointer;
    background: $submit-btn-catalog;
    border: none;

    @include tablet {
      width: 44px;
      height: 44px;
    }

    @include desktop {
      width: 48px;
      height: 48px;
    }
  }

  &_submit-button-svg {
    @include mobile {
      pointer-events: none;
      width: 14px;
      height: 14px;
    }

    @include tablet {
      width: 18px;
      height: 18px;
    }

    @include desktop {
      width: 20px;
      height: 20px;
    }
  }
}

.search-block_submit-btn:hover {
  background: linear-gradient(
    141.22deg,
    rgba(246, 178, 7, 0.612) 9.4%,
    rgba(248, 65, 25, 0.5) 91.91%
  );
}

.movies-list {
  @include mobile {
    margin-left: -8px;
    margin-right: -8px;
  }

  @include tablet {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}

.movie-item {
  position: relative;
  margin-bottom: 16px;
  border-radius: 5px;
  width: 100%;

  @include mobile {
    transition: transform 0.3s ease;
    overflow: hidden;
  }

  @include tablet {
    flex-basis: calc(33.3333% - 16px);
    margin-bottom: 28px;
    border-radius: 5px;
  }

  @include desktop {
    margin-bottom: 32px;
    border-radius: 5px;
  }

  &:nth-child(21) {
    margin-bottom: 0;
  }

  &:nth-of-type(n + 22) {
    display: none;
  }

  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) 63.48%,
      rgba(0, 0, 0, 0.9) 92.16%
    );
    transition: transform 0.01s ease-in-out;
    cursor: pointer;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  }

  &:hover {
    transform: scale(1.05);
    transition: transform 0.35s ease-in-out;
  }
}

.movie-details {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0 12px 12px 12px;
  box-sizing: border-box;
  color: $mainLightColor;
  display: block;
  flex-wrap: wrap;
  justify-content: space-between;

  @include tablet {
    padding: 0 0 12px 12px;
    display: flex;
  }

  @include desktop {
    display: flex;
  }

  & h3 {
    @include mobile {
      font-size: 12px;
      line-height: 1.67;
      text-transform: uppercase;
      color: $mainLightColor;
    }

    @include tablet {
      font-size: 14px;
      line-height: 1.28;
    }

    @include desktop {
      font-size: 20px;
      line-height: 1.15;
    }
  }
}

@media screen and (min-width: 480px) and (max-width: 767.97px) {
  .movie-details h3,
  .movie-info {
    transform: translateY(110%);
  }
}

.movie-genres-and-rating {
  @include mobile {
    display: contents;
  }

  @include tablet {
    display: flex;
    align-items: end;
    justify-content: center;
    flex-wrap: nowrap;
    overflow: hidden;
  }
}

.movie-info {
  @include mobile {
    font-size: 12px;
    color: $pagination-background;
    margin-top: 6px;
  }

  @include tablet {
    font-size: 14px;
    width: 112px;
  }

  @include desktop {
    font-size: 20px;
    width: 250px;
  }
}

// ! Ð—Ð²ÐµÐ·Ð´Ñ‹ Ñ€ÐµÐ¹Ñ‚Ð¸Ð½Ð³Ð°
.star-full {
  width: 18px;
  color: darkorange;
}

.movie-rating {
  text-align: end;
}

