.page-indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  padding-bottom: 12px;

  @include tablet {
    margin-top: 44px;
    padding-bottom: 30px;
  }

  @include desktop {
    margin-top: 60px;
    padding-bottom: 70px;
  }
}

.page-number {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 32px;
  text-align: center;
  margin: 0 4px;
  cursor: pointer;
  border-radius: 50%;

  @include mobile {
    border: 1px solid $button-black-text;
    background: transparent;
    color: $pagination-number-color;
    color: var(--mainDarkColor);
  }

  @include tablet {
    width: 40px;
    height: 40px;
    margin: 0 8px;
  }

  &.active {
    background: $pagination-color;
    color: $mainLightColor;
  }
}

.page-number:hover {
  border-color: var(--footerBgColor);
}

.arrow-button {
  cursor: pointer;
  background: var(--background-catalog);

  &.disabled {
    opacity: 0.5;
  }
}

.arrow-disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.prev-page-svg,
.next-page-svg {
  cursor: pointer;
}

.page-number {
  display: flex;
  justify-content: center;
  align-items: center;
}

// !! Ð¡Ñ‚Ð¸Ð»Ð¸ Ð¼Ð¾Ð´Ð°Ð»ÐºÐ¸
.input-search-input {
  @include mobile {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 16px;
    margin-bottom: 48px;
  }

  @include tablet {
    margin-bottom: 80px;
  }

  @include desktop {
    gap: 24px;
    margin-bottom: 120px;
  }
}

.modal,
.modal-overlay {
  display: none;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 4px;
  opacity: 0;
  animation: fade-in 0.25s ease-in-out forwards;
}

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.9);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1);
  }
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}

.visible {
  display: block;
}

.modal.visible {
  opacity: 1;
}

.search-modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}

.search-modal-content {
  @include mobile {
    width: 480px;
    height: 300px;
    background-color: var(--background-catalog);
    margin: 15% auto;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 1px 14px 4px #ff6b086b;
  }

  @include tablet {
    width: 550px;
    height: 500px;
    font-size: 24px;
    line-height: 1.33;
  }

  @include desktop {
    height: 500px;
    width: 650px;
    font-size: 32px;
    line-height: 1.12;
  }

  &_text-modal {
    @include mobile {
      font-size: 20px;
      line-height: 1.4;
      font-family: Roboto;
      font-weight: 400;
      text-align: center;
      color: var(--backgroundPage);
      color: var(--title-color);
    }

    @include tablet {
      font-size: 24px;
      line-height: 1.33;
    }

    @include desktop {
      font-size: 32px;
      line-height: 1.12;
    }
  }
}

.search-modal-close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  position: absolute;
  top: 15px;
  right: 20px;
}

.search-modal-close:hover,
.search-modal-close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

#search-loading {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9999;
  display: none;
}

#search-loading::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 60px;
  height: 60px;
  margin-top: -30px;
  margin-left: -30px;
  border-radius: 50%;
  border: 6px solid #f3f3f3;
  border-top-color: #3498db;
  animation: spin 5s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
