.weeklytrends_nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 24px;
  @include desktop {
    margin-bottom: 28px;
  }
}
.weeklytrends_title {
  color: var(--title-color);
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18.75px;
  text-transform: uppercase;
  @include tablet {
    font-size: 24px;
    line-height: 28.13px;
  }

  @include desktop {
    font-size: 28px;
    line-height: 32.81px;
  }
}

.weeklytrends_seeAll_link {
  color: $seeAllBtnColor;
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18.75px;
  transition: all $duration cubic-bezier(0.4, 0, 0.2, 1);
  @include tablet {
    font-size: 24px;
    line-height: 28.13px;
  }

  @include desktop {
    font-size: 28px;
    line-height: 32.81px;
  }
}

.weeklytrends_seeAll_link:hover {
  color: $informationColor;
}

.weeklytrends_gallery_list {
  display: flex;
  justify-content: center;
  gap: 16px;
}
.weeklytrends_gallery_image {
  border-radius: 5px;
}
.weekly-trends--flex {
  flex-direction: column;
}

div.weekly-trends--start {
  justify-content: flex-start;
}

div.weekly-trends--card {
  @media screen and (max-width: 767px) {
    width: 280px;
  }
}

div.weekly-trends--card:nth-child(-n + 2) {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.container--weekly-trends {
  @media screen and (max-width: 767px) {
    width: 320px;
  }
}
