*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root[data-theme="light"] {
  --body-and-modals-bgc: #fff;
  --before-postionX: 2px;
  --before-postionY: 2px;
  --mainDarkColor: #000;
  --footerBgColor: #f5f5f5;
  --footerTxtColor: #595959;
  --backgroundPage: transparent;
  --logo-name-color: #282828;
  --swipe-bullet-color: #595959;
  --swipe-bullet-color-active: #282828;
  --swipe-nav: #595959;
  --swipe-nav-disabled: #595959;
  --title-color: #111;
  --description-color: #282828;
  --header-nav-color: #595959;
  --team-sm-bnt-accent: #ff6b01;
  --background-catalog: #fff;
  --upcoming-film-categories-check-color: #111;
  --upcomind-film-df-color: #282828;
  --upcoming-film-bulb-color: #f8f8f8;
  --upcoming-film-descr-text-color: #282828;
  --modal-trailer-bgc: #fff;
  --close-btn-fill: #282828;
  --modal-trailer-text-color: #111;
  --modal-trailer-box-shadow-color: rgba(0, 0, 0, .22);
}

:root[data-theme="dark"] {
  --body-and-modals-bgc: #000;
  --before-postionX: 28px;
  --before-postionY: 2px;
  --footerBgColor: #000;
  --footerTxtColor: #b7b7b7;
  --backgroundPage: #fff;
  --logo-name-color: #fff;
  --swipe-bullet-color: #b7b7b7;
  --swipe-bullet-color-active: #fff;
  --swipe-nav-disabled: #b7b7b7;
  --swipe-nav: #b7b7b7;
  --title-color: #fff;
  --description-color: #f8f8f8;
  --header-nav-color: #b7b7b7;
  --team-sm-bnt-accent: #932727;
  --background-catalog: #000;
  --upcoming-film-categories-check-color: #b7b7b7;
  --upcomind-film-df-color: #fff;
  --upcoming-film-bulb-color: #fff;
  --upcoming-film-descr-text-color: #f8f8f8;
  --mainDarkColor: #fff;
  --modal-trailer-bgc: #111;
  --close-btn-fill: #fff;
  --modal-trailer-text-color: #fff;
  --modal-trailer-box-shadow-color: rgba(255, 107, 8, .42);
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul, li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  color: var(--mainDarkColor);
  background-color: var(--body-and-modals-bgc);
  margin: 0 auto;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, .56);
  transition: opacity .25s, visibility .25s;
  position: fixed;
  top: 0;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-thumb {
  background-color: #932727;
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #aaa;
}

::-moz-scrollbar {
  width: 8px;
  height: 8px;
}

::-moz-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 5px;
}

::-moz-scrollbar-thumb:hover {
  background-color: #aaa;
}

