.header {
  max-width: 1600px;
  height: 48px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0 auto;
  padding-top: 12px;
  padding-bottom: 12px;
  position: relative;
}

.header__wraper {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.logo__site {
  color: #fff;
  fill: #fff;
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.logo__site:hover, .logo__site:focus {
  transform: scale(1.25);
}

.logo__site:hover .logo__name {
  color: #ff6b08;
}

.logo__icon {
  fill: var(--icon-color);
}

.logo__name, .header-navigation {
  display: none;
}

.navigation-list {
  flex-wrap: wrap;
  flex-grow: 0;
  display: flex;
}

.navigation-list__link {
  text-transform: uppercase;
  color: var(--header-nav-color);
  font-size: 14px;
  font-weight: 500;
  line-height: 1.667;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.navigation-list__link:hover {
  color: #ff6b08;
  cursor: pointer;
  outline: none;
}

.navigation-list__link-current {
  color: #ff6b08;
}

.navigation-list__item:not(:last-child) {
  margin-right: 32px;
}

.mobile-menu {
  width: 204px;
  height: 100vh;
  opacity: 0;
  background-color: var(--footerBgColor);
  border-radius: 0 8px 8px 0;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 20px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.mobile-menu.is-open {
  opacity: 1;
  transform: translateX(0%);
}

.menu-logo {
  margin-bottom: 44px;
}

.mob-navigation-list__item:not(:last-child) {
  margin-bottom: 24px;
}

.mob-navigation-list__link {
  color: var(--menu-text-color);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.17;
}

.mob-navigation-list__link-current {
  color: #ff6b08;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(255, 255, 255, .2);
  position: fixed;
  top: 0;
  right: 0;
}

.backdrop.is-open {
  opacity: 1;
  pointer-events: all;
}

